import {faCircleCheck, faCircleXmark} from "@fortawesome/free-solid-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import React, {useMemo} from "react";
import {AbilityDatabase, useAbilityDatabase} from "../../../database/v2/AbilityDatabase";
import {FeatDatabase, useFeatDatabase} from "../../../database/v2/FeatDatabase";
import {Formula} from "../../../logic/Formula";
import {CharacterAtLevel} from "../../../model/character/CharacterAtLevel";

interface PrerequisiteListProps {
  featId: string;
  formulaText: string;
  characterAtLevel: CharacterAtLevel;
}

export default function PrerequisiteList({ featId, formulaText, characterAtLevel } : PrerequisiteListProps) {
  const abilityDatabase = useAbilityDatabase();
  const featDatabase = useFeatDatabase();

  const lines = useMemo(() => {
    return formulaText.split('AND')
    .map(clause => clause.trim())
    .filter(clause => clause !== '!@' + featId)
    .map(clause => {
      const resolved = Formula.parse(clause).resolve(characterAtLevel);
      let statusMark = <FontAwesomeIcon style={{ color: 'green' }} icon={faCircleCheck} />;
      if (!(resolved?.asBoolean() ?? true)) {
        statusMark = <FontAwesomeIcon style={{ color: 'red' }} icon={faCircleXmark} />
      }
      return <>{statusMark} {formatPrerequisiteClause(clause, abilityDatabase, featDatabase, characterAtLevel)}</>;
    });
  }, [formulaText, characterAtLevel]);

  return (<div>
    {lines.map(line => <div>{line}</div>)}
  </div>);
}

function formatPrerequisiteClause(clause: string, abilityDatabase: AbilityDatabase, featDatabase: FeatDatabase, characterAtLevel: CharacterAtLevel): JSX.Element {
  const callSelf = (clause: string) => formatPrerequisiteClause(clause, abilityDatabase, featDatabase, characterAtLevel);

  const matches = clause.match(/\((.*) OR (.*)\)/);
  if (matches !== null) {
    return <span>{matches.slice(1).map(group => <>{callSelf(group)}</>).reduce((a, b) => <>{a} <i>or</i> {b}</>)}</span>;
  }

  if (clause.startsWith("!")) {
    return <>{'Not ' + callSelf(clause.substring(1))}</>;
  }
  if (clause.startsWith("@feat:")) {
    const featId = clause.substring(1);
    const featName = abilityDatabase.summary(featId)?.name ?? featId;
    return <><b>{featName}</b> feat</>;
  }
  if (clause.startsWith("@ability:")) {
    const abilityId = clause.substring(1);
    const abilityName = abilityDatabase.summary(abilityId)?.name ?? abilityId;
    return <><b>{abilityName}</b> ability</>;
  }
  if (clause.startsWith("sum(@ability:")) {
    const abilityId = clause.substring("sum(@".length, clause.length - 1);
    const abilityName = abilityDatabase.summary(abilityId)?.name ?? abilityId;
    return <><b>{abilityName}</b> ability</>;
  }
  if (clause.startsWith("@bab")) {
    return callSelf("Base Attack Bonus +" + clause.substring("@bab >= ".length));
  }
  if (clause.startsWith("@str_score >= ")) {
    return callSelf("Strength " + clause.substring("@str_score >=  ".length-1));
  }
  if (clause.startsWith("@dex_score >= ")) {
    return callSelf("Dexterity " + clause.substring("@dex_score >=  ".length-1));
  }
  if (clause.startsWith("@con_score >= ")) {
    return callSelf("Constitution " + clause.substring("@con_score >=  ".length-1));
  }
  if (clause.startsWith("@int_score >= ")) {
    return callSelf("Intelligence " + clause.substring("@int_score >=  ".length-1));
  }
  if (clause.startsWith("@wis_score >= ")) {
    return callSelf("Wisdom " + clause.substring("@wis_score >=  ".length-1));
  }
  if (clause.startsWith("@cha_score >= ")) {
    return callSelf("Charisma " + clause.substring("@cha_score >=  ".length-1));
  }
  if (clause.startsWith("@caster_level >= ")) {
    return callSelf("Caster Level " + clause.substring("@caster_level >= ".length-1));
  }
  return <>{clause}</>;
}