import React, {ReactElement, ReactNode, useCallback, useContext, useEffect, useRef, useState} from "react";
import {Accordion, AccordionContext, useAccordionButton} from "react-bootstrap";
import {AccordionEventKey} from "react-bootstrap/AccordionContext";
import {useOnScreen} from "../../app/reactHooks";
import "./PathfinderSelect.scss";

interface PathfinderSelectProps {
  activeKey?: string;
  onChange?: (key: string|undefined) => void;
  children: ReactElement<PathfinderSelectItemProps>|ReactElement<PathfinderSelectItemProps>[];
}

const PathfinderSelect = ({ activeKey, onChange, children }: PathfinderSelectProps) => {
  function handleChangeSelection(eventKey: AccordionEventKey) {
    onChange?.(typeof eventKey === "string" ? eventKey : undefined);
  }

  return <Accordion activeKey={activeKey}
                   className={'pf-select'}
                   onSelect={handleChangeSelection}
                   flush={false}>
    {children}
  </Accordion>
}

interface PathfinderSelectItemProps {
  itemKey: string;
  label: ReactNode;
  bodyFn?: () => Promise<ReactNode>;
  body?: ReactNode;
  disabled?: boolean;
}

function PathfinderSelectItem({ itemKey, label, bodyFn, body, disabled = false }: PathfinderSelectItemProps) {
  const hasBody = body !== undefined || bodyFn !== undefined;
  return hasBody
      ? <PathfinderSelectItemWithBody itemKey={itemKey} label={label} bodyFn={bodyFn} body={body} disabled={disabled} />
      : <PathfinderSelectItemWithoutBody itemKey={itemKey} label={label} disabled={disabled} />
}

function PathfinderSelectItemWithoutBody({ itemKey, label, disabled }: PathfinderSelectItemProps) {
  const { activeEventKey } = useContext(AccordionContext);
  const isActive = itemKey === activeEventKey;
  const selectOptionButton = useAccordionButton(itemKey);

  const itemClassNames = [ 'pf-select-item' ];
  if (isActive) {
    itemClassNames.push('pf-select-item--active');
  }
  if (disabled) {
    itemClassNames.push('pf-select-item--invalid');
  }

  return (
      <div className={itemClassNames.join(' ')}>
        <div className={'pf-select-item-button'} onClick={selectOptionButton}>{label}</div>
      </div>);
}

function PathfinderSelectItemWithBody({ itemKey, label, bodyFn, body, disabled }: PathfinderSelectItemProps) {
  const elementRef = useRef<HTMLDivElement>(null);
  const onScreen = useOnScreen(elementRef);
  const { activeEventKey } = useContext(AccordionContext);

  const isActive = itemKey === activeEventKey;
  const [ expanded, setExpanded ] = useState(isActive);
  const [ expanding, setExpanding ] = useState(false);
  const [ collapsing, setCollapsing ] = useState(false);

  const [ bodyState, setBodyState ] = useState<ReactNode>(body);
  useEffect(() => {
    if (!onScreen || bodyFn === undefined) {
      return;
    }

    let loading = true;
    let mounted = true;
    bodyFn().then(content => {
      if (mounted) {
        setBodyState(content);
      }
      loading = false;
    });
    return () => {
      mounted = false;
      if (!loading) {
        setBodyState(body);
      }
    }
  }, [itemKey, onScreen])

  const elementShownRef = useCallback((node: HTMLDivElement) => {
    node?.scrollIntoView({
      behavior: "auto",
      block: "center",
    });
  }, []);

  const selectOptionButton = useAccordionButton(itemKey);

  const handleExpanding = () => {
    setExpanding(true);
    setExpanded(false);
    setCollapsing(false);
  }

  const handleExpanded = () => {
    setExpanding(false);
    setExpanded(true);
  }

  const handleCollapsing = () => {
    setExpanding(false);
    setExpanded(false);
    setCollapsing(true);
  }

  const handleCollapsed = () => {
    setCollapsing(false);
    setExpanded(false);
  }

  const itemClassNames = [ 'pf-select-item' ];
  if (expanded) {
    itemClassNames.push('pf-select-item--expanded');
  }
  if (expanding) {
    itemClassNames.push('pf-select-item--expanding');
  }
  if (collapsing) {
    itemClassNames.push('pf-select-item--collapsing');
  }
  if (isActive) {
    itemClassNames.push('pf-select-item--active');
  }
  if (disabled) {
    itemClassNames.push('pf-select-item--invalid');
  }

  return (
      <div className={itemClassNames.join(' ')} ref={elementRef}>
        <div className={'pf-select-item-button'} onClick={selectOptionButton}>{label}</div>
        <Accordion.Collapse eventKey={itemKey}
                            appear={isActive}
                            // style={{ display: hasBody ? 'block' : 'none' }}
                            onEntering={_ => handleExpanding()}
                            onEntered={_ => handleExpanded()}
                            onExiting={_ => handleCollapsing()}
                            onExited={_ => handleCollapsed()}>
          <div className={'pf-select-item-body'} ref={elementShownRef}>
            {bodyState}
          </div>
        </Accordion.Collapse>
      </div>);
}

PathfinderSelect.Item = PathfinderSelectItem;

export default PathfinderSelect;