import React from "react";
import {Modal} from "react-bootstrap";
import {SkillEditor} from "./SkillEditor";

interface SkillEditorDialogProps {
  show: boolean;
  onCancel?: () => void;
}

export default function SkillEditorDialog({ show, onCancel }: SkillEditorDialogProps) {
  return <Modal
      show={show}
      onHide={onCancel}
      aria-labelledby="contained-modal-title-vcenter"
      centered={true}
      size={'lg'}
      scrollable={true}
      className={'pf-feat-selector-dialog'}
  >
    <Modal.Header className={'title'} closeButton={true} closeVariant={'white'}>
      <Modal.Title>Edit Skills</Modal.Title>
    </Modal.Header>

    <Modal.Body>
      <SkillEditor />
    </Modal.Body>
  </Modal>
}