import {useState} from "react";
import * as Icon from "react-bootstrap-icons";
import PathfinderButton from "../../common/PathfinderButton";
import SkillEditorDialog from "./SkillEditorDialog";

export default function SkillEditorButton() {
  const [show, setShow] = useState(false);

  const handleShow = () => setShow(true);
  const handleCancel = () => setShow(false);

  const buttonLabel = <><Icon.PencilSquare/>&nbsp; Edit Skills</>;

  return (<>
    <PathfinderButton variant={'white'} onClick={_ => handleShow()}>{buttonLabel}</PathfinderButton>
    {show && <SkillEditorDialog show={show} onCancel={handleCancel} />}
  </>);
}